.about {
  padding: 110px 0 140px;
}

@media screen and (max-width: 1023px) {
  .about {
    padding: 90px 0 110px;
  }
}

.about__title {
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: -0.04em;
  border-bottom: 1px solid #000;
  padding-bottom: 23px;
  margin: 0;
}

@media screen and (max-width: 767px) {
  .about__title {
    margin: 0 0 60px;
  }
}

.about__info {
  display: flex;
  gap: 40px;
  padding-bottom: 110px;
}

@media screen and (max-width: 767px) {
  .about__info {
    flex-direction: column;
    gap: 56px;
  }
}

.about__info_container {
  display: flex;
  flex-direction: column;
  gap: 26px;
  margin: 70px 0 0;
}

@media screen and (max-width: 767px) {
  .about__info_container {
    margin: 0;
  }
}

.about__info_title {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: -0.04em;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 767px) {
  .about__info_title {
    font-size: 18px;
    line-height: 22px;
  }
}

.about__info_paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 767px) {
  .about__info_paragraph {
    font-size: 11px;
    line-height: 16px;
  }
}

.about__progressbar {
  width: 100%;
  display: flex;
}

.about__progressbar_left {
  flex: 1;
  background-color: #2be080;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

@media screen and (max-width: 767px) {
  .about__progressbar_left {
    flex: 2;
  }
}

.about__progressbar_left_text {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

@media screen and (max-width: 767px) {
  .about__progressbar_left {
    font-size: 11px;
    line-height: 13px;
  }
}

.about__progressbar_left_paragraph {
  margin: 0;
  padding: 0;
  bottom: -28px;
  position: absolute;
  color: #a0a0a0;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.about__progressbar_right {
  flex: 3;
  background-color: #f2f2f2;
  padding: 9px 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about__progressbar_right_text {
  margin: 0;
  padding: 0;
}

.about__progressbar_right_paragraph {
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: -28px;
  color: #a0a0a0;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}
